html,body{
    padding:0;
    margin: 0;
    box-sizing: border-box;
}

.page-content{
    
    display:grid;
    grid-template-rows: auto 1fr;
    grid-template-columns: auto 1fr;;
    height: 100vh;
    width: 100vw;
}

.header{
    grid-row: 1/2;
    grid-column: 2/-1;
    background-color: #333333;
    padding: 1.5rem;
    color: white;
}


.sidebar{

    grid-column: 1/2;
    grid-row: 1/-1;
    background-color: #4A90E2 ;
    padding: 1rem;  
    width: 15vw;
}



.content{
    grid-row: 2/-1;
    grid-column: 2/-1;
    background-color: #F5F5F5 ;
    padding: 5rem;
}


ul{
    display:flex;
    flex-direction: column;
    gap: 2rem;
}


.todo:hover{
    background-color:#FF6F6199 ;
}

.project-name{
    font-size: 2rem;
    font-weight: bold;
}


.todos{
    margin-top: 2rem;
    display:flex;
    flex-direction: column;
    gap: 1rem;
    font-size: 1.5rem;
    font-family:cursive;
}

.todo{
    display:flex;
    gap: 1rem;
}

.todo-delete{
    margin-left:auto;
}

.projects{
    display:flex;
    flex-direction: column;
    gap: 1rem;


}

.project-button{
    border: none;
    font-size: 1.2rem;
    background-color: #4A90E2;
    color: #333333;
    position:relative;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    

}

.project-button:hover{
    background-color: #33333388;;
    color: white;

}

.project-button::before{
    content: '#';
    color: white;

    position:absolute;
    left: 1rem;
}


.add-icon{
    width: 2rem;
    height: 2rem;
}


.add-todo-button{
    cursor: pointer;    border: none;

}

.add-Todo{
    display:flex;
    gap : 30px;
    margin-top: 2rem;
}

.add-Todo:hover{
    color: black;
    font-size: bold;;
    background-color: #FF6F6166;
    cursor: pointer;
}


form{
    border: 2px solid black;
    display:flex;
    flex-direction: column;
    gap: 5px;
    padding: 1rem;
    
}

input{
    border: none;
    outline: none;
}

textarea{
    resize:none;
    outline:none;
}

.submit{
    width: 5rem;
}

.form-buttons{
    margin-left: auto;
    gap: 10px;
    display:flex;

}

.hidden{
    display:none;
}

button img{
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
    
    
}

.my-projects button{
    background-color:#4A90E2;
    border: none;
    
}

.my-projects{
    display:flex;
    font-size: 1.5rem;
}


#project-form img{

    width: 1.2rem;
    height: 1.2rem;

}

/* #project-form button{
    
    background-color:#4A90E2 ;
    border: none;
} */



#project-form{
    border:none;

}

#project-form input{
    border-radius: 12px;
    height: 1.1rem;
    padding: 10px;
    font-size: 1.1rem;
}

.project-buttons{
    display:flex;
    gap: 10px;
}

.div-form{
    margin-top: 1rem;
}

.div-form textarea {

    padding: 6px;
    font-size: 1rem;
    font-family:Verdana, Geneva, Tahoma, sans-serif

}

.description , .task-name{
    display:flex;
    align-items: center;
    gap: 1rem;
}


h2{
    font-family:Verdana, Geneva, Tahoma, sans-serif

}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":";AACA;IACI,SAAS;IACT,SAAS;IACT,sBAAsB;AAC1B;;AAEA;;IAEI,YAAY;IACZ,4BAA4B;IAC5B,+BAA+B;IAC/B,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,yBAAyB;IACzB,eAAe;IACf,YAAY;AAChB;;;AAGA;;IAEI,gBAAgB;IAChB,cAAc;IACd,0BAA0B;IAC1B,aAAa;IACb,WAAW;AACf;;;;AAIA;IACI,cAAc;IACd,iBAAiB;IACjB,0BAA0B;IAC1B,aAAa;AACjB;;;AAGA;IACI,YAAY;IACZ,sBAAsB;IACtB,SAAS;AACb;;;AAGA;IACI,2BAA2B;AAC/B;;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;;;AAGA;IACI,gBAAgB;IAChB,YAAY;IACZ,sBAAsB;IACtB,SAAS;IACT,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,SAAS;AACb;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,sBAAsB;IACtB,SAAS;;;AAGb;;AAEA;IACI,YAAY;IACZ,iBAAiB;IACjB,yBAAyB;IACzB,cAAc;IACd,iBAAiB;IACjB,+CAA+C;;;AAGnD;;AAEA;IACI,2BAA2B;IAC3B,YAAY;;AAEhB;;AAEA;IACI,YAAY;IACZ,YAAY;;IAEZ,iBAAiB;IACjB,UAAU;AACd;;;AAGA;IACI,WAAW;IACX,YAAY;AAChB;;;AAGA;IACI,eAAe,KAAK,YAAY;;AAEpC;;AAEA;IACI,YAAY;IACZ,UAAU;IACV,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,2BAA2B;IAC3B,eAAe;AACnB;;;AAGA;IACI,uBAAuB;IACvB,YAAY;IACZ,sBAAsB;IACtB,QAAQ;IACR,aAAa;;AAEjB;;AAEA;IACI,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,SAAS;IACT,YAAY;;AAEhB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,cAAc;IACd,eAAe;;;AAGnB;;AAEA;IACI,wBAAwB;IACxB,YAAY;;AAEhB;;AAEA;IACI,YAAY;IACZ,iBAAiB;AACrB;;;AAGA;;IAEI,aAAa;IACb,cAAc;;AAElB;;AAEA;;;;GAIG;;;;AAIH;IACI,WAAW;;AAEf;;AAEA;IACI,mBAAmB;IACnB,cAAc;IACd,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,SAAS;AACb;;AAEA;IACI,gBAAgB;AACpB;;AAEA;;IAEI,YAAY;IACZ,eAAe;IACf;;AAEJ;;AAEA;IACI,YAAY;IACZ,mBAAmB;IACnB,SAAS;AACb;;;AAGA;IACI;;AAEJ","sourcesContent":["\r\nhtml,body{\r\n    padding:0;\r\n    margin: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.page-content{\r\n    \r\n    display:grid;\r\n    grid-template-rows: auto 1fr;\r\n    grid-template-columns: auto 1fr;;\r\n    height: 100vh;\r\n    width: 100vw;\r\n}\r\n\r\n.header{\r\n    grid-row: 1/2;\r\n    grid-column: 2/-1;